import React from "react";
import Table from "react-bootstrap/Table";
import CustomSelect from "../Select/customSelect";
import "./staticTable.css";

const StaticTable = ({
  columnHeaders,
  handleCheckboxChange,
  checkedItems,
  handleSelectChange,
  selectedAttributes,
  mappedAttributes, // Passed mapped attributes
  mandatoryAttributes,
}) => {
  console.log("Mapped Attributes prop:", mappedAttributes);
  console.log("columnHeaders", columnHeaders);
  console.log("static table recieved manditory Attribute", mandatoryAttributes);

  return (
    <Table bordered className="table-light ">
      <thead>
        <tr>
          <th>Column Name</th>
          <th>Attribute Name</th>
        </tr>
      </thead>
      <tbody>
        {columnHeaders.map((header, index) => {
          const isMandatory = mandatoryAttributes.includes(header);
          return (
            <tr key={index} className={isMandatory ? "disabled-row" : ""}>
              <td>{header}</td>
              <td>
                <CustomSelect
                  index={index}
                  selectedValue={selectedAttributes[index] || header} // ✅ Default value is the column header
                  onChange={handleSelectChange}
                  size="lg"
                  className="w-100"
                  disabled={isMandatory}
                  options={{
                    defaultLabel: header,
                    // this is for mandatory attributes
                    // defaultLabel: header -> //comparea header with nls and show// ✅ Default option shows the column header but if mandatory attribute irrespective of the api response, for non mandatory attributes we need to check the api response if similiar header/value exists then set the dfauly header same.

                    list: mappedAttributes.map((attr) => ({
                      value: attr.backendName,
                      label: attr.uiLabel, // ✅ UI Label from backend
                    })),
                  }}
                />
              </td>
            </tr>
          );
        })}
      </tbody>
    </Table>
  );
};

export default StaticTable;
